<template>
  <!-- 头部整体盒子 -->
  <div id="header" class="container-fuild">
    <!-- 头部顶部 -->
    <!-- <div class="header-top container-fuild hidden-xs">
        <div class="container">
          <div class="server pull-left">
            <span class="glyphicon glyphicon-earphone"></span>888-888-888
            <span class="glyphicon glyphicon-envelope"></span>xxx@163.com
            <span class="glyphicon glyphicon-time"></span>7x24小时为您服务
          </div>
          <div class="shejiao pull-right">
            <span class="glyphicon glyphicon-hand-right"></span>赶快联系我们吧！
            <span class="glyphicon glyphicon-hand-left"></span>
          </div>
        </div>
      </div> -->
    <!-- 电脑导航 -->
    <div class="header-nav container hidden-xs">
      <!-- 导航logo -->
      <div class="header-nav-logo">
        <img src="@/assets/img/logo_black.png">
      </div>
      <!-- 导航内容 -->
      <ul class="header-nav-wrapper">
        <li v-for="(item, index) in navList" :key="index" :class="index == navIndex ? 'active' : ''"
          @click="navClick(index, item)">
          <router-link :to="item.path">
            {{ item.name }}
            <span v-if="item.children.length > 0" class="glyphicon glyphicon-menu-down"></span>
            <i class="underline"></i>
          </router-link>
          <dl v-if="item.children.length > 0">
            <dt v-for="(i, n) in item.children" :key="n">
              <router-link :to="i.path">{{ i.name }}</router-link>
            </dt>
          </dl>
        </li>
      </ul>
    </div>
    <!-- 手机导航 -->
    <div style="display: flex;" class="header-nav-m container-fuild visible-xs">
      <div style="width: 30%;float: left;" class="header-nav-m-logo">
        <img class="center-block" src="@/assets/img/logo_black.png" alt="logo">
      </div>
      <!-- 导航栏 -->
      <div style="width: 70%;float: right;" @click="menuClick" class="header-nav-m-menu text-center">
        <div class="header-nav-m-menu-wrapper" data-toggle="collapse" data-target="#menu">
          <img style="width: 19.39px;" src="../assets/img/cebian.png" alt="" srcset="">
        </div>
        <ul id="menu" class="header-nav-m-wrapper collapse nav">
          <li v-for="(item, index) in navList" :key="index" :class="index == navIndex ? 'active' : ''"
            @click="navClick(index, item)" data-toggle="collapse" data-target="active">
            {{ item.name }}
            <i class="underline"></i>
          </li>
        </ul>
      </div>
    </div>
    <van-action-sheet v-model="show" :actions="actions" @select="onSelect" />
  </div>
</template>
<script>
export default {
  name: "Headeren",
  data() {
    return {
      show: false,
      actions: [{ id: 1, name: '中文繁體' }],
      navIndex: sessionStorage.getItem('navIndex') ? sessionStorage.getItem('navIndex') : 0,
      menuName: "首页",
      menuClass: "glyphicon glyphicon-menu-down",
      navList: [
        {
          name: "Home",
          path: "/",
          children: []
        },
        {
          name: "Service order",
          path: "/service_en",
          children: []
        },
        {
          name: "News",
          path: "/newsinformation_en",
          children: []
        },
        {
          name: "About as",
          path: "/companyintroduction_en",
          children: []
        },
        {
          name: "Language",
          path:'',
          children: []
        },
      ]
    };
  },
  methods: {
    onSelect() {
      this.$emit('push',1)
      this.show = false;
    },
    navClick(index, item) {
      if (item.name === "Language") {
        this.show = true
      } else {
        this.navIndex = index;
        sessionStorage.setItem('navIndex', index)
        this.menuName = item.name;
        this.$router.push(item.path)
      }
    },
    menuClick() {
      if (this.menuClass == "glyphicon glyphicon-menu-down") {
        this.menuClass = "glyphicon glyphicon-menu-up";
      } else {
        this.menuClass = "glyphicon glyphicon-menu-down";
      }
    }
  }
};
</script>
<style scoped>
.navbar{
  z-index: 9999;
    /* 相对定位 */
    position: relative;
    /* 100%窗口宽度 */
    width: 20vw;
    display: flex;
    right: 0;
}
#checkbox{
    display: none;
}
.navbar label{
    position: absolute;
    top: 0;
    left: 70px;
    width: 100%;
    height: 43px;
    font-size: 30px;
    color: #5a738e;
    background-color: #ededed;
    padding-left: 20px;
    border: 1px solid #d9dee4;
    cursor: pointer;
    /* 加个动画过渡 */
    transition: all 0.5s;
}
.navbar ul{
    list-style: none;
    width: 100%;
    background-color: #2a3f54;
    transition: all 0.5s;
    overflow: hidden;
}
.navbar ul li{
    margin-bottom: 10px;
}
.navbar ul li:first-child{
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0 10px;
}
.navbar ul li:first-child img{
    width: 50px;
    height: 50px;
    border-radius: 50%;
}
.navbar ul li:first-child span{
    /* 先把文字隐藏起来 */
    display: none;
    color: #fff;
    /* 文字禁止换行 */
    white-space: nowrap;
    padding-left: 10px;
}
.navbar ul li a{
    display: flex;
    justify-content: center;
    align-items: center;
    /* 垂直排列 */
    flex-direction: column;
    width: 100%;
    height: 100%;
    color: #d1d1d1;
    text-decoration: none;
}
.navbar ul li a i{
    font-size: 25px;
    margin-bottom: 10px;
}
.navbar ul li a span{
    font-size: 12px;
    white-space: nowrap;
}
.navbar ul li a:hover{
    color: #fff;
    background-color: #35495d;
}
/* :checked选择器是判断复选框是否被选中 */
/* +是相邻兄弟选择器，找到input的下一个兄弟label */
.navbar input:checked + label{
    left: 200px;
}
/* ~也是兄弟选择器，但它可以找到隔了几代的兄弟 */
.navbar input:checked ~ ul{
    width: 200px;
}
.navbar input:checked ~ ul li:first-child{
    /* 将居中改为开头对齐 */
    justify-content: flex-start;
}
.navbar input:checked ~ ul li:first-child span{
    /* 文字显示 */
    display: block;
}
.navbar input:checked ~ ul li a{
    flex-direction: row;
    justify-content: flex-start;
}
.navbar input:checked ~ ul li a i{
    font-size: 18px;
    margin: 0 15px 0 30px;
}
.navbar input:checked ~ ul li a span{
    font-size: 14px;
}
/*  */
.nav li {
  z-index: 999999;
  /* 导航栏大小 */
  width: 150px;
  height: 60px;
  /* 去浮动，使li横向布局 */
  float: right;
  /* 每个li之间间隙 */
  margin-right: 10px;
  /* 去点 */
  list-style: none;
  /* 背景颜色 */
  background-color: gray;
  /* 行高，使文本居中 */
  line-height: 60px;
  text-align: center;
  /* 阴影，使导航栏有层次感 */
  box-shadow: -5px 5px 0 rgba(0, 0, 0, 0.3);
}

.nav li ul li {
  /* 去点 */
  list-style: none;
  /* 下拉菜单大小 */
  /* 去浮动，使li横向布局 */
  float: left;
  /* 背景颜色 */
  background-color: chocolate;
  /* 阴影，使导航栏有层次感 */
  border: 5px rgba(0, 0, 0, 0.1) solid;
  /* margin-top: 5px; 此代码可使下拉菜单有间隔，更美观，但无法实现hover效果*/
}

.nav li:hover {
  /* 鼠标 */
  cursor: pointer;
  /* 背景颜色 */
  background-color: #50536e;
  /* 阴影，使导航栏有层次感 */
  box-shadow: -10px 10px 0 rgba(0, 0, 0, 0.6);
  /* 执行时间 */
  transition: .3s;
}

/* 隐藏 */
.nav li ul li {
  display: none;
}

/* 显示 */
.nav li:hover ul li {
  display: block;
  transition: 0.3s;
  cursor: pointer;
}

.nav li ul li:hover {
  cursor: pointer;
  background-color: sandybrown;
}

/* 顶部 */
#header {
  background: #f4f4f4;
  transition: all ease 0.6s;
}

#header .header-top {
  height: 50px;
  color: #fff;
  font-size: 12px;
  line-height: 50px;
  background: #474747;
}

/* 顶部的图标 */
#header .header-top span {
  margin: 0 8px;
}

/* 导航栏 */
#header .header-nav {
  height: 110px;
}

/* 导航栏logo */
#header .header-nav .header-nav-logo {
  width: 100px;
  height: 100%;
  float: left;
  position: relative;
}

/* 导航栏logo图片 */
#header .header-nav .header-nav-logo img {
  width: 95px;
  height: 45px;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
}

/* 导航栏 导航容器 */
#header .header-nav-fixed .header-nav-wrapper {
  line-height: 50px;
}

#header .header-nav .header-nav-wrapper {
  line-height: 110px;
  float: right;
  margin: 0;
  max-width: 800px;
}

/* 导航栏 每个导航 */
#header .header-nav .header-nav-wrapper>li {
  float: left;
  margin: 0 15px;
  position: relative;
}

/* 导航栏 每个导航下面的 a 链接 */
#header .header-nav .header-nav-wrapper>li>a {
  color: #000;
  font-size: 15px;
  font-weight: bold;
  padding: 15px 0;
  position: relative;
}

/* 导航栏 每个导航下面的 a 链接的下划线 */
#header .header-nav .header-nav-wrapper>li>a>i {
  display: block;
  position: absolute;
  bottom: -2px;
  left: 50%;
  width: 0;
  height: 2px;
  opacity: 0;
  transition: all 0.6s ease;
  background-color: #1e73be;
}

/* 导航栏 每个导航下面的 a 链接的右侧小三角 */
#header .header-nav .header-nav-wrapper>li>a>span {
  font-size: 12px;
  transition: transform ease 0.5s;
}

/* 导航栏 每个导航下面的 a 链接 鼠标滑上去的样式 */
#header .header-nav .header-nav-wrapper>li>a:hover {
  color: #1e73be;
  text-decoration: none;
}

/* 导航栏 每个导航下面的 a 链接 鼠标滑上去下划线的样式 */
#header .header-nav .header-nav-wrapper>li>a:hover .underline {
  opacity: 1;
  width: 100%;
  left: 0;
}

/* 导航栏 每个导航下面的 a 链接 鼠标滑上去三角标的样式 */
#header .header-nav .header-nav-wrapper>li>a:hover span {
  transform: rotate(180deg);
}

/* 导航栏 每个导航下面的 a 链接 鼠标点击后的样式 */
#header .header-nav .header-nav-wrapper>li.active>a {
  color: #1e73be;
  text-decoration: none;
  border-bottom: 2px solid #1e73be;
}

/* 导航栏 每个导航下面的二级导航容器 */
#header .header-nav .header-nav-wrapper>li>dl {
  display: none;
  position: absolute;
  width: 168px;
  top: 80%;
  left: 0;
  z-index: 999999;
  box-shadow: 0 0 3px 1px #ccc;
  background: #fff;
}

/* 导航栏 每个导航下面的二级导航容器的每个导航 */
#header .header-nav .header-nav-wrapper>li>dl>dt {
  width: 100%;
  padding: 10px;
  border-bottom: 1px solid #ccc;
}

/* 导航栏 每个导航下面的二级导航容器的每个导航 当鼠标滑上时的样式*/
#header .header-nav .header-nav-wrapper>li>dl>dt>a:hover {
  text-decoration: none;
}

/* 导航栏 滑上一级导航显示二级导航 */
#header .header-nav .header-nav-wrapper>li:hover dl {
  display: block;
}

#header .header-nav .header-nav-wrapper>li>dl>dt:hover {
  cursor: pointer;
  background: #ccc;
}

@media screen and (max-width: 997px) {
  #header .header-nav-m {
    position: relative;
  }

  /* 导航栏logo容器 */
  #header .header-nav-m .header-nav-m-logo {
    height: 44px;
    position: relative;
  }

  /* 导航栏logo图片 */
  #header .header-nav-m .header-nav-m-logo img {
    width: 79.8px;
    height: 28px;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    margin: auto;
  }

  /* 导航栏  菜单容器 */
  #header .header-nav-m .header-nav-m-menu {
    color: #fff;
    height: 44px;
    font-size: 20px;
    line-height: 50px;
    position: relative;
  }

  /* 导航栏 菜单图标 */
  #header .header-nav-m .header-nav-m-menu-wrapper {
    position: absolute;
    top: 50%;
    right: 20px;
    margin-top: -10px;
    width: 50px;
    height: 40px;
    color: #fff;
    z-index: 999999;
    font-size: 12px;
  }

  /* 导航栏 */
  #header .header-nav-m .header-nav-m-wrapper {
    position: absolute;
    top: 50px;
    /* background: #474747; */
    z-index: 9999999;
  }

  /* 导航栏 每个导航 */
  #header .header-nav-m .header-nav-m-wrapper>li {
    height: 40px;
    line-height: 40px;
    border-bottom: 1px solid #ccc;
  }

  /* 导航栏 每个导航下面的 a 链接 */
  #header .header-nav-m .header-nav-m-wrapper>li>a {
    color: #fff;
    font-size: 15px;
    font-weight: bold;
    padding: 15px 0;
    position: relative;
  }

  /* 导航栏 每个导航下面的 a 链接的右侧小三角 */
  #header .header-nav .header-nav-wrapper>li>a>span {
    font-size: 10px;
  }
}
</style>


